body,
html,
div#root,
#root,
h1
{
  font-family: 'Nunito', sans-serif;
  margin: 0;
  padding: 0;
}
h2 {
  font-size: 24px;
  font-weight: 700;
  color: #6c757d;
  font-family: 'Nunito', sans-serif;
}
h3 {
  font-size: 19px;
  font-weight: 700;
  color: #6c757d;
  font-family: 'Nunito', sans-serif;
}
h5 {
  font-size: 15px;
  color: #98a6ad;
  font-family: 'Nunito', sans-serif;
  font-weight: 600;
