body {
  background-color: $bg-color-light;
  font-family: 'Nunito', sans-serif;
}

.body-container {
  display: flex;
  flex-direction: row;
  margin-top: 72px;
  margin-left: 250px;

  @media only screen and (max-width: 992px) { 
    margin-left: 0px;
  }
}

button:hover {
  -webkit-box-shadow: 0px 0px 7px 1px #bbb;
  -moz-box-shadow: 0px 0px 7px 1px #bbb;
  box-shadow: 0px 0px 7px 1px #bbb;
}

.btn-primary {
  background-color: $primary-color;
  border-color: $primary-color;
  &:focus {
    border-color: inherit;
    border: none !important;
    outline: none !important;
    -webkit-box-shadow: 0px 0px 7px 1px #bbb;
    -moz-box-shadow: 0px 0px 7px 1px #bbb;
    box-shadow: 0px 0px 7px 1px #bbb;
  }
  &:active, &:hover {
    background-color: rgba(251, 114, 153, 0.9) !important;
    border-color: inherit;
    -webkit-box-shadow: 0px 0px 10px 2px rgba(120, 120, 120, .2);
    -moz-box-shadow: 0px 0px 10px 2px rgba(120, 120, 120, .2);
    box-shadow: 0px 0px 10px 2px rgba(120, 120, 120, .2)
  }
}

.btn-secondary {
  border: none;
  background-color: rgb(241,241,244);
  color: rgb(43,44,40);
  &:focus {
    border-color: inherit;
    border: none !important;
    outline: none !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
  }
  &:active, &:hover {
    // background-color: rgba(241,241,244,.5) !important;
    border: none !important;
    outline: none !important;
    -webkit-box-shadow: 0px 0px 10px 2px rgba(120, 120, 120, .2);
    -moz-box-shadow: 0px 0px 10px 2px rgba(120, 120, 120, .2);
    box-shadow: 0px 0px 10px 2px rgba(120, 120, 120, .2);
  }
}

.btn-info {
  // background-color: $primary-blue;
  background-color: rgba(0,161,214, .8);
  border: none;
  &:focus {
    border-color: inherit;
    border: none !important;
    outline: none !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
  }
  &:active, &:hover, &:disabled {
    background-color: rgb(0,161,214) !important;
    border: none !important;
    outline: none !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
  }
}

.btn-success {
  background-color: $primary-green;
  border: none;
  &:focus {
    border-color: inherit;
    border: none !important;
    outline: none !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
  }
  &:active, &:hover {
    background-color: #2ec08a !important;
    border: none !important;
    outline: none !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
  }
}

//Modal
.modal-content{
  border: none;
  border-radius: 10px;
  button {
    background-color:  $primary-color;
    border: none;
    padding: 10px 30px;
    border-radius: 20px;
  }
  .modal-header {
    background-color: $primary-color;
    border: none;
    border-radius: 10px 10px 0 0 ;
    padding: 25px 40px;
    .modal-title {
      font-size: 19px;
      font-weight: 700;
      color: white;
      font-family: 'Nunito', sans-serif;
    }
    button.close {
      color: white;
      &:hover {
        border: none;
        box-shadow: none;
      }
    }
  }
  .modal-body {
    border: none;
    padding: 25px 40px;
    form {
      margin: 30px 0px 25px 0px;
      input {
        border: 1px solid rgba(0,0,0,0.1); 
        padding: 20px;
        &::placeholder {
          color:rgba(0,0,0,0.4);
        }
      }
      .form-group {
        margin: 20px 0;
      }
    }
  }
  .modal-footer {
    padding: 20px 40px;
    #close {
      color: #474d56;
      background-color: #d4deeb;
      border-color: #cbd7e7;
      margin-right: 15px;
    }
    .btn-primary {
      background-color: $primary-color;
    }
  }
}

// Alert
.alert {
  p {
    margin: 0;
  }
  button {
    &:hover {
      border-color: inherit;
      border: none !important;
      outline: none !important;
      box-shadow: none !important;
      -webkit-box-shadow: none !important;
    }
    &:focus {
      border-color: inherit;
      border: none !important;
      outline: none !important;
      box-shadow: none !important;
      -webkit-box-shadow: none !important;
    }
  }
}

