@import "../../index.scss";
@import "../../../node_modules/react-input-range/lib/css/index.css";
@import url('https://fonts.googleapis.com/css2?family=Archivo+Black&family=Limelight&display=swap');

.stopwatch-container {
  .content {
    margin: 0 auto;
    width: 100%;
    padding: 50px 30px;

    .stopwatch-header {
      margin-bottom: 30px;
    }
    .stopwatch-body {
      border-radius: 40px;
      box-shadow: 2px 2px 10px 0 rgba(32,33,36, 0.1);
      width: 100%;
      height: 600px;
      background-color: white;
      max-width: 800px;
      margin: 0 auto;

      .background {
          width: 100%;
          height: auto;
          display: block;
          img {
            border-radius: 40px 40px 0 0;
            width: 100%;
            height: auto;
            max-height: 350px;
            object-fit: cover;
          }
      }

      .stopwatch {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 50px 0 20px;
        font-size: 80px;
        color: $primary-color;
        font-family: 'Archivo Black', cursive, sans-serif;
        @media only screen and (max-width: 576px) { 
          font-size: 60px;
        }
      }

      .buttons {
        width: 90%;
        margin: 0 auto;
        display: flex;
        justify-content: flex-end;
        span {
          font-size: 30px;
          cursor: pointer;
          margin-left: 10px;
          color: $primary-color;
        }
      }
    }
  }
}

